.Answer {
  list-style: none;
  margin: 0;
  padding: 0;
}

.Answer li {
  padding: 10px;
  border-left: 4px solid #ff3b3b;
  background-color: #bbb;
  margin: 0 0 10px;
  border-radius: 10px;
}
.Answer .choosed {
  border-left: 4px solid #1a1a1a !important;
}

.Answer li:hover {
  cursor: pointer;
  background-color: #a2a2a2;
}

.Answer .correct,
.Answer .correct:hover {
  background-color: #1db900;
  border-left: 4px solid #0f6300;
  color: #fff;
}

.Answer .incorrect,
.Answer .incorrect:hover {
  background-color: #ec0000;
  border-left: 4px solid #ff3b3b;
  color: #fff;
}

.Answer[disabled] {
  pointer-events: none;
  opacity: 0.7;
}
